.color{color: #002f34;}
.header{
  top: 0px;
  left: 0px;
  right: 0px;
  height: 70px;
  z-index: #f1f1f1;
  box-shadow: 0px 2px 2px #dadada;
  padding: 0px 18%;
}
.header .logo{width: 50px; margin-right: 20px;}
.header .logo img{
  width: 100%;
}
.header .location{background: #ffffff; border: 2px #002f34 solid; border-radius: 5px;}
.header .location .icon{padding: 10px;}
.header .location .label{flex: 1; padding: 10px; border: 0px;}
.header .location .arro{flex: 1; padding: 10px; border: 0px; background: #ffffff;}
.header .search{background: #ffffff; border: 2px #333 solid; border-radius: 5px; flex: 2; margin: 0px 20px;}
.header .search .querry{flex: 1; padding: 10px; border: 0px;}
.header .search .go{flex: 1; padding: 10px; border: 0px; background: #002f34; max-width: 50px;}
